  --color-tag-work-bg: #eff6ff;
  --color-tag-work-border: #3b82f6;
  --color-tag-work-text: #3b82f6;

  --color-tag-personal-bg: #f0fdf4;
  --color-tag-personal-border: #10b981;
  --color-tag-personal-text: #10b981;

  --color-tag-urgent-bg: #fff7ed;
  --color-tag-urgent-border: #f59e0b;
  --color-tag-urgent-text: #f59e0b;

  --color-tag-important-bg: #faf5ff;
  --color-tag-important-border: #8b5cf6;
  --color-tag-important-text: #8b5cf6;

  /* Badge Colors */
  --color-badge-due-today-bg: #fef2f2;
  --color-badge-due-today-border: #ffc9c9;
  --color-badge-due-today-text: #dc2626;

  --color-badge-recurrence-bg: #faf5ff;
  --color-badge-recurrence-border: #e9d4ff;
  --color-badge-recurrence-text: #9810fa;

  /* State Colors */
  --color-completed-text: #a3a3a3;
  --color-checkbox-checked: #3b82f6;

  /* Semantic Colors */
  --color-success: #10b981;
  --color-error: #ef4444;
  --color-warning: #f59e0b;

  /* Typography */
  --font-family-base: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;

