  --color-tag-important-bg: #faf5ff;
  --color-tag-important-border: #8b5cf6;
  --color-tag-important-text: #8b5cf6;

  /* Badge Colors */
  --color-badge-due-today-bg: #fef2f2;
  --color-badge-due-today-border: #ffc9c9;
  --color-badge-due-today-text: #dc2626;

  --color-badge-recurrence-bg: #faf5ff;
  --color-badge-recurrence-border: #e9d4ff;
  --color-badge-recurrence-text: #9810fa;

  /* State Colors */
  --color-completed-text: #a3a3a3;
  --color-checkbox-checked: #3b82f6;

  /* Semantic Colors (same) */
  --color-success: #10b981;
  --color-error: #ef4444;
  --color-warning: #f59e0b;

  /* Shadows - darker for dark theme */
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.5);
  --shadow-modal: 0 20px 25px rgba(0, 0, 0, 0.6);
}

/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
*:not(dialog) {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 8. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

/* opinionated globals */

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

a,
a:visited {
  text-decoration: none;
}

/* base styles */

body {
  font-family: var(--font-family-base);
  background-color: var(--color-bg-app);
  color: var(--color-text-primary);
  height: 100%;
  margin: 0;
}

body {
  line-height: var(--line-height-normal);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-regular);
}

/* Typography */
h1 {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
  line-height: var(--line-height-tight);
}

h2 {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  line-height: var(--line-height-tight);
}

h3 {
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-semibold);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--color-text-muted);
}

/* Utility Classes */
.text-secondary {
  color: var(--color-text-secondary);
}

.text-muted {
  color: var(--color-text-muted);
}

/* Focus Styles */
button:focus-visible,
input:focus-visible,
textarea:focus-visible,
select:focus-visible {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}

/* Transitions */
button,
a {
  transition: all var(--transition-fast);
}

